import { a } from '@react-spring/three';
import { Billboard, RoundedBox, Text } from '@react-three/drei';
import ellipsize from 'ellipsize';
import type { FC } from 'react';
import React, { useMemo } from 'react';
import type { ColorRepresentation, Euler } from 'three';
import { Color } from 'three';

const calculateTextSize = (
  text: string,
  fontSize: number,
  maxWidth: number,
  ellipsis: number,
  active: boolean
) => {
  const shortText = ellipsis && !active ? ellipsize(text, ellipsis) : text;
  const lines = [];
  let currentLine = '';
  const words = shortText.split(' ');

  words.forEach(word => {
    const testLine = currentLine ? `${currentLine} ${word}` : word;
    const testWidth = testLine.length * fontSize * 1;

    if (testWidth > maxWidth) {
      lines.push(currentLine);
      currentLine = word;
    } else {
      currentLine = testLine;
    }
  });

  if (currentLine) {
    lines.push(currentLine);
  }

  const width =
    Math.min(
      maxWidth,
      lines.reduce(
        (max, line) => Math.max(max, line.length * fontSize * 0.4),
        0
      )
    ) + 14;
  const height = lines.length * fontSize + 6;

  return { width, height, text: lines.join('\n'), lineCount: lines.length };
};

export interface LabelProps {
  /**
   * Text to render.
   */
  text: string;

  /**
   * Font URL.
   * Reference: https://github.com/reaviz/reagraph/issues/23
   */
  fontUrl?: string;

  /**
   * Size of the font.
   */
  fontSize?: number;

  /**
   * Color of the text.
   */
  color?: ColorRepresentation;

  /**
   * Stroke of the text.
   */
  stroke?: ColorRepresentation;

  /**
   * Opacity for the label.
   */
  opacity?: number;

  /**
   * The lenth of which to start the ellipsis.
   */
  ellipsis?: number;

  /**
   * Whether the label is active ( dragging, hover, focus ).
   */
  active?: boolean;

  /**
   * Rotation of the label.
   */
  rotation?: Euler | [number, number, number];

  /**
   * Maximum width of the label.
   */
  maxWidth?: number;

  /**
   * Background color of the label.
   */
  backgroundColor?: ColorRepresentation;

  /**
   * Border radius of the label.
   */
  borderRadius?: number;

  /**
   * Type of the label.
   */
  type?: 'node' | 'edge';

  /**
   * label visible or not
   */
  labelVisible?: boolean;
}

export const Label: FC<LabelProps> = ({
  text,
  fontSize = 4,
  fontUrl,
  color = '#2A6475',
  opacity = 1,
  stroke,
  backgroundColor,
  active,
  rotation,
  maxWidth = 100,
  ellipsis = 100,
  borderRadius
}) => {
  const normalizedColor = useMemo(() => new Color(color), [color]);
  const normalizedBackgroundColor = useMemo(
    () => new Color(backgroundColor),
    [backgroundColor]
  );
  const normalizedStroke = useMemo(
    () => (stroke ? new Color(stroke) : undefined),
    [stroke]
  );

  const {
    width,
    height,
    text: processedText,
    lineCount
  } = useMemo(
    () => calculateTextSize(text, fontSize, maxWidth, ellipsis, active),
    [text, fontSize, maxWidth, ellipsis, active]
  );

  return (
    <Billboard>
      {backgroundColor ? (
        <mesh>
          <RoundedBox
            position={[0, lineCount * -2, 10]}
            args={[width, height, 0]} // Width, height, depth.
            radius={borderRadius}
            rotation={rotation}
          >
            <Text
              font={fontUrl}
              fontSize={fontSize}
              color={normalizedColor}
              fillOpacity={opacity}
              textAlign="center"
              outlineWidth={stroke ? 1 : 0}
              outlineColor={stroke ? normalizedStroke : null}
              depthOffset={0}
              maxWidth={maxWidth}
              overflowWrap="break-word"
            >
              {processedText}
            </Text>
            <a.meshBasicMaterial
              attach="material"
              opacity={opacity}
              depthTest={true}
              color={normalizedBackgroundColor}
            />
          </RoundedBox>
        </mesh>
      ) : (
        <Text
          font={fontUrl}
          fontSize={fontSize}
          color={normalizedColor}
          fillOpacity={opacity}
          textAlign="center"
          outlineWidth={stroke ? 1 : 0}
          outlineColor={normalizedStroke}
          depthOffset={0}
          maxWidth={maxWidth}
          overflowWrap="break-word"
          rotation={rotation}
        >
          {processedText}
        </Text>
      )}
    </Billboard>
  );
};

